   .container{
   max-width: 700px;
   margin: 0px auto;
   padding: 0 20px;
   background-color: antiquewhite;
   
   }
   
   .form{
   
   }
   .form__labelText{
   display: inline-block;
   max-width: 150px;
   width: 100%;
   margin-right: 5px;
   }
   
   .form__field{
   border: 2px solid rgb(16, 116, 39);
   padding: 10px;
   max-width: 400px;
   width: 100%;
   border-radius: 5px;
   }
   .form__field:invalid{
   
       background-color: brown;
   }
   .form__button{
       width: 100%;
       background-color: rgb(80, 185, 35);
       border: none;
       padding: 10px;
       border-radius: 5px;
   }
   .form__button:hover{
       background-color: rgb(165, 195, 153);
    
   }
   
   .form__button:active{
       background-color: rgb(221, 224, 220);
    
   }
   
   .form__fieldset{
   border: 1px solid rgb(22, 234, 7);
   padding: 20px;
   border-radius: 5px;
   margin: 20px 0;
   }
   
   .form__legend{
       background-color: rgb(28, 116, 19);
       border-radius: 5px;
       padding: 5px;
       color: azure;
   }